'use client'

import { useState, useEffect } from 'react'
import { useRouter, useSearchParams } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'
import { Label } from '@/components/ui/label'
import BottomNavigation from '@/components/BottomNavigation'
import { useJourney } from '@/contexts/JourneyContext'

const meditationOptions = [
  { value: 'regularly', label: 'Yes, regularly' },
  { value: 'occasionally', label: 'Occasionally' },
  { value: 'never', label: 'Never' }
]

const moodOptions = [
  { value: 'happy', emoji: 'üòä', label: 'Happy' },
  { value: 'neutral', emoji: 'üòê', label: 'Neutral' },
  { value: 'sad', emoji: 'üò¢', label: 'Sad' },
  { value: 'angry', emoji: 'üò†', label: 'Frustrated' },
  { value: 'tired', emoji: 'üò¥', label: 'Tired' }
]

export default function QuestionnairePage() {
  const router = useRouter()
  const searchParams = useSearchParams()
  const { selectedJourney, setSelectedJourney } = useJourney()
  const [support, setSupport] = useState('')
  const [meditation, setMeditation] = useState('')
  const [mood, setMood] = useState('')
  const [errors, setErrors] = useState<string[]>([])

  useEffect(() => {
    // Get journey from URL params and save it
    const journeyParam = searchParams.get('journey')
    if (journeyParam) {
      setSelectedJourney(journeyParam)
    }
  }, [searchParams, setSelectedJourney])

  // Journey-specific support options
  const getSupportOptions = () => {
    switch (selectedJourney) {
      case 'ttc':
        return [
          { value: 'fertility-anxiety', label: 'Fertility Anxiety' },
          { value: 'patience', label: 'Building Patience' },
          { value: 'hope', label: 'Maintaining Hope' }
        ]
      case 'pregnant':
        return [
          { value: 'pregnancy-anxiety', label: 'Pregnancy Anxiety' },
          { value: 'body-changes', label: 'Body Changes Support' },
          { value: 'bonding', label: 'Bonding with Baby' }
        ]
      case 'miscarriage':
        return [
          { value: 'grief', label: 'Grief Support' },
          { value: 'healing', label: 'Healing Process' },
          { value: 'self-blame', label: 'Releasing Self-Blame' }
        ]
      case 'stillbirth':
        return [
          { value: 'profound-grief', label: 'Profound Grief Support' },
          { value: 'memory-honoring', label: 'Honoring Baby\'s Memory' },
          { value: 'isolation', label: 'Overcoming Isolation' }
        ]
      case 'postpartum':
        return [
          { value: 'overwhelm', label: 'Managing Overwhelm' },
          { value: 'identity', label: 'Identity Changes' },
          { value: 'sleep-deprivation', label: 'Sleep Deprivation Support' }
        ]
      default:
        return [
          { value: 'general', label: 'General Support' },
          { value: 'anxiety', label: 'Anxiety Management' },
          { value: 'confidence', label: 'Building Confidence' }
        ]
    }
  }

  const getJourneyLabel = () => {
    const journeyLabels = {
      'ttc': 'Trying to Conceive',
      'pregnant': 'Pregnant',
      'miscarriage': 'Experienced a Miscarriage',
      'stillbirth': 'Experienced a Stillbirth',
      'postpartum': 'Postpartum'
    }
    return journeyLabels[selectedJourney as keyof typeof journeyLabels] || 'Your Journey'
  }

  const handleSubmit = () => {
    const newErrors: string[] = []
    
    if (!support) newErrors.push('Please select the type of support you\'re looking for')
    if (!meditation) newErrors.push('Please let us know about your meditation experience')
    if (!mood) newErrors.push('Please share how you\'re feeling today')

    if (newErrors.length > 0) {
      setErrors(newErrors)
      return
    }

    // Clear errors and proceed
    setErrors([])
    router.push('/home')
  }

  return (
    <div className="min-h-screen px-6 py-12 pb-24 bg-gradient-to-br from-rose-50 via-purple-50 to-amber-50">
      <div className="max-w-2xl mx-auto">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-3xl font-playfair text-gray-800 mb-4">
            Let's Personalize Your Experience
          </h1>
          <p className="text-gray-600">
            Help us understand how we can best support you on your {getJourneyLabel().toLowerCase()} journey
          </p>
        </div>

        {/* Error Messages */}
        {errors.length > 0 && (
          <Card className="mb-6 border-red-200 bg-red-50">
            <CardContent className="p-4">
              <ul className="text-red-600 text-sm space-y-1">
                {errors.map((error, index) => (
                  <li key={index}>‚Ä¢ {error}</li>
                ))}
              </ul>
            </CardContent>
          </Card>
        )}

        <div className="space-y-8">
          {/* Question 1: Support Type */}
          <Card>
            <CardHeader>
              <CardTitle className="text-xl text-gray-800">
                What kind of support are you looking for?
              </CardTitle>
            </CardHeader>
            <CardContent>
              <RadioGroup value={support} onValueChange={setSupport}>
                {getSupportOptions().map((option) => (
                  <div key={option.value} className="flex items-center space-x-2">
                    <RadioGroupItem value={option.value} id={option.value} />
                    <Label htmlFor={option.value} className="text-gray-700 cursor-pointer">
                      {option.label}
                    </Label>
                  </div>
                ))}
              </RadioGroup>
            </CardContent>
          </Card>

          {/* Question 2: Meditation Experience */}
          <Card>
            <CardHeader>
              <CardTitle className="text-xl text-gray-800">
                Have you used meditation before?
              </CardTitle>
            </CardHeader>
            <CardContent>
              <RadioGroup value={meditation} onValueChange={setMeditation}>
                {meditationOptions.map((option) => (
                  <div key={option.value} className="flex items-center space-x-2">
                    <RadioGroupItem value={option.value} id={`med-${option.value}`} />
                    <Label htmlFor={`med-${option.value}`} className="text-gray-700 cursor-pointer">
                      {option.label}
                    </Label>
                  </div>
                ))}
              </RadioGroup>
            </CardContent>
          </Card>

          {/* Question 3: Current Mood */}
          <Card>
            <CardHeader>
              <CardTitle className="text-xl text-gray-800">
                How are you feeling today?
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                {moodOptions.map((option) => (
                  <button
                    key={option.value}
                    onClick={() => setMood(option.value)}
                    className={`p-4 rounded-lg border-2 transition-all duration-200 ${
                      mood === option.value
                        ? 'border-rose-400 bg-rose-50'
                        : 'border-gray-200 bg-white hover:border-gray-300'
                    }`}
                  >
                    <div className="text-2xl mb-2">{option.emoji}</div>
                    <div className="text-sm text-gray-700">{option.label}</div>
                  </button>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Submit Button */}
          <div className="flex space-x-4">
            <Button
              variant="outline"
              onClick={() => router.back()}
              className="flex-1"
            >
              Back
            </Button>
            <Button
              onClick={handleSubmit}
              className="flex-1 bg-rose-400 hover:bg-rose-500 text-white"
            >
              Continue
            </Button>
          </div>
        </div>
      </div>

      <BottomNavigation />
    </div>
  )
}
